*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Inter", sans-serif;
  font-size: 16px;
}

body {
  width: 100%;
  min-height: 100vh;
  background-color: #e5e5e5;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* START NEW PROJECTS */

/* .flexy{
  display: flex;
  justify-content: center;
  align-items: center;
} */

.journal {
  width: 550px;
  background-color: #ffffff;
}

.header {
  width: 100%;
  height: 55px;
  background-color: #f55a5a;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header--title {
  font-size: 15px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: -0.075em;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.header--icon {
  max-width: 100%;
  width: 20px;
  margin-right: 0.3rem;
}

/* MAIN */

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 46px 0;
  gap: 20px;
}

/* CARD */

.card {
  width: 470px;
  display: flex;
  align-items: center;
  padding-bottom: 20px;
}

.card:not(:last-child){
  border-bottom: 1px solid #F5F5F5;
}

.card--picture {
  flex: 0 0 auto;
  width: 125px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 168px;
  border-radius: 5px;
}

.card--info {
  padding-left: 20px ;
  color: #2b283a;
}

.location-pin {
  width: 10px;
}

.card--location {
  display: flex;
  align-items: center;
  margin-bottom: 13px;
}

.card--location span {
  font-size: 10.24px;
  line-height: 12px;
  letter-spacing: 0.17em;
  text-transform: uppercase;
}

.card--link {
  margin-left: 12px;
  font-size: 11px;
  line-height: 12px;
  text-decoration-line: underline;
  color: #918e9b;
}
.card--title {
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  color: #2b283a;
  margin-bottom: 14px;
}

.card--date {
  font-weight: bold;
  font-size: 11px;
  line-height: 12px;
  color: #2b283a;
}
.card--description {
  font-size: 10.24px;
  line-height: 150%;
  color: #2b283a;
  margin-top: 9px;
}
